<template>
  <form class="form-password-recovery" @submit.prevent="send">
    <div class="logo-top-popup"></div>
    <h1 class="form-password-recovery__caption">{{ 'passwordRecovery' | translate }}</h1>
    <div v-if="!isSended">
      <!-- Email -->
      <div class="ir-form-group">
        <label for="email" class="ir-form-label">{{ 'email' | translate }}<span class="required">*</span></label>
        <input
          id="email"
          type="text"
          class="ir-input-field"
          :class="{ error: errors.email }"
          v-model="user.email"
          :placeholder="'email' | translate"
        />
        <p v-show="errors.email" class="ir-input-field-error">{{ errors.email | translate }}</p>
      </div>
      <!-- Submit button -->
      <div class="form-password-recovery__submit">
        <button
          type="submit"
          class="ir-blue-button restore-button"
          :class="{ 'disabled': !isActiveSubmitButton }"
        >
          {{ 'passwordRecoverySend' | translate }}
        </button>
      </div>
    </div>    
    <div v-if="isSended">
      <!-- Sended text -->
      <p class="form-password-recovery__sended-text">
        {{ 'passwordRecoverySendedText' | translate }}
        <span class="user-email">{{ user.email }}</span>
      </p>
      <!-- Check E-mail button -->
      <div class="form-password-recovery__check-email">
        <button
          v-if="isSended"
          type="submit"
          class="ir-blue-button check-email"
        >
          {{ 'passwordRecoveryCheckEmail' | translate }}
        </button>
      </div>
    </div>
    <BottomLoginBox />
  </form>
</template>

<script>
import BottomLoginBox from '@/components/BottomLoginBox.vue'

export default {
  name: 'FormPasswordRecovery',
  components: {
    BottomLoginBox
  },
  data () {
    return {
      user: {
        email: ''
      },
      errors: {
        email: ''
      },
      isSended: false
    }
  },
  computed: {
    isActiveSubmitButton () {
      return this.user.email
    }
  },
  methods: {
    send () {
      if (!this.isActiveSubmitButton) return
      if (!this.validate()) return

      // Call send recovery email api here
      // На ответ сервера о высылке пароля переключить флаг isSended
      this.isSended = true

    },
    validate () {
      let result = true
      if (!this.validateEmail()) result = false

      return result
    },
    validateEmail () {
      this.errors.email = ''
      // Check required
      if (!this.user.email) {
        this.errors.email = 'mustbeEmail'
        return false
      }
      // Check RegExp
      const res = /^([a-z0-9_-]+\.)*[a-z0-9_-]+@[a-z0-9_-]+(\.[a-z0-9_-]+)*\.[a-z]{2,6}$/.test(this.user.email)
      if (!res) {
        this.errors.email = 'invalidEmail'
        return false
      }

      return true
    }
  }
}
</script>

<style lang="scss">
.form-password-recovery {
  position: fixed;
  width: 424px;
  height: 424px;
  padding: 0 68px;
  background: url('../assets/img/ir/bg-recovery.png') 100% 100% no-repeat;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  .logo-top-popup {
    transform: translateX(-49%);
    top: 10px;
  }
  &__caption {
    font-size: 36px;
    text-align: center;
    margin-top: 84px;
    margin-bottom: 20px;
    font-family: 'B52';
    font-size: 36px;
    font-weight: normal;
    letter-spacing: -1.35px;
    line-height: 1;
    text-align: center;
    color: #5b4746;
  }
  &__submit {
    width: 202px;
    margin: 30px auto 0 auto;
    .restore-button {
      height: 44px;
    }
  }
  &__sended-text {
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    color: #000;
    font-family: 'RobotoSlab';
    font-size: 15px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.6;
    letter-spacing: 0.14px;
    text-align: center;
    color: #5b4746;
    .user-email {
      color: #D77D4F;
    }
  }
  &__check-email {
    width: 170px;
    margin: 30px auto 0 auto;
    .check-email {
      height: 44px;
    }
  }
}
</style>
