<template>
  <form class="form-login" @submit.prevent="login">
    <div class="logo-top-popup"></div>
    <h1 class="form-login__caption">{{ 'authorization' | translate }}</h1>
    <!-- Social links -->
    <!--
    <SocialLinks action="login" />
    -->
    <!-- Email -->
    <div class="ir-form-group">
      <label for="email" class="ir-form-label">{{ 'email' | translate }}<span class="required">*</span></label>
      <input
        id="email"
        type="text"
        class="ir-input-field"
        :class="{ error: errors.email }"
        v-model="user.email"
        :placeholder="'email' | translate"
      />
      <p v-show="errors.email" class="ir-input-field-error">{{ errors.email | translate }}</p>
    </div>
    <!-- Password -->
    <div class="ir-form-group">
      <label for="password" class="ir-form-label">{{ 'password' | translate }}<span class="required">*</span></label>
      <input
        id="password"
        :type="!passwordShow ? 'password' : 'text'"
        class="ir-input-field password"
        :class="{ error: errors.password }"
        v-model="user.password"
        :placeholder="'password' | translate"
      />
      <span :class="[ !passwordShow ? 'view-password-btn' : 'hide-password-btn' ]" @click="passwordShow = !passwordShow"></span>
      <p v-show="errors.password" class="ir-input-field-error">{{ errors.password | translate }}</p>
    </div>
    <!-- Submit button -->
    <div class="form-login__submit-box">
      <span class="form-login__text">
        <router-link to="/password-recovery">
          {{ 'forgotPassword' | translate }}
        </router-link>    
      </span>
      <div class="form-login__submit">
        <button
          type="submit"
          class="ir-blue-button"
          :class="{ 'disabled': !isActiveSubmitButton }"
        >
          {{ 'login' | translate }}
        </button>
        <span class="decorate-line"></span>
      </div>
    </div>
    <!-- Register button -->
    <div class="form-login__register-box">
      <span class="form-login__text">
        Ещё нет аккунта?
      </span>
      <router-link to="/register" class="ir-orange-button register-button">
        {{ 'registration' | translate }}
      </router-link>
    </div>
  </form>
</template>

<script>
// import SocialLinks from '@/components/SocialLinks.vue'

export default {
  name: 'FormLogin',
  components: {
    // SocialLinks
  },
  data () {
    return {
      user: {
        email: '',
        password: ''
      },
      errors: {
        email: '',
        password: ''
      },
      passwordShow: false
    }
  },
  computed: {
    isActiveSubmitButton () {
      return this.user.email && this.user.password
    }
  },
  methods: {
    login () {
      if (!this.isActiveSubmitButton) return
      if (!this.validate()) return

      // Call login api here

      this.$router.push('/')
    },
    validate () {
      let result = true
      if (!this.validateEmail()) result = false
      if (!this.validatePassword()) result = false

      return result
    },
    validateEmail () {
      this.errors.email = ''
      // Check required
      if (!this.user.email) {
        this.errors.email = 'mustbeEmail'
        return false
      }
      // Check RegExp
      const res = /^([a-z0-9_-]+\.)*[a-z0-9_-]+@[a-z0-9_-]+(\.[a-z0-9_-]+)*\.[a-z]{2,6}$/.test(this.user.email)
      if (!res) {
        this.errors.email = 'invalidEmail'
        return false
      }

      return true
    },
    validatePassword () {
      this.errors.password = ''
      // Check required
      if (!this.user.password) {
        this.errors.password = 'mustbePassword'
        return false
      }

      // Check length
      if (this.user.password.length < 6 || this.user.password.length > 20) {
        this.errors.password = 'invalidPassword'
        return false
      }

      // Check RegExp
      const res = /^[a-zA-Z0-9\W]+$/.test(this.user.password)
      if (!res) {
        this.errors.password = 'invalidPassword'
        return false
      }

      return true
    }
  }
}
</script>

<style lang="scss">
.form-login {
  position: fixed;
  width: 416px;
  height: 455px;
  padding: 0 68px;
  background: url('../assets/img/ir/bg-login.png') 100% 100% no-repeat;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  &__caption {
    font-size: 36px;
    text-align: center;
    margin-top: 74px;
    margin-bottom: 20px;
    font-family: 'B52';
    font-size: 36px;
    font-weight: normal;
    letter-spacing: -1.35px;
    text-align: center;
    color: #5b4746;
  }
  &__submit-box, &__register-box {
    margin-top: 30px;
  }
  &__register-box {
    .register-button {
      width: 119px;
    }
  }
  &__text {
    display: inline-block;
    width: calc(100% - 119px);
    text-align: left;
    font-family: RobotoSlab;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: 0.13px;
    color: #5b4746;
    & > a {
      color: #b4512b;
      text-decoration: none;
      font-family: RobotoSlab;
      font-size: 14px;
      font-weight: bold;
      letter-spacing: 0.13px;
      &:hover {
        text-decoration: underline;
      }
    }
  }
  &__submit {
    display: inline-block;
    position: relative;
    width: 119px;
    height: 40px;
    /*
    &:hover {
      cursor: default;
    }
    */
  }
  &__register-button {
    display: block;
    text-align: center;
    font-size: 18px;
    color: #6E6E6E;
    text-decoration: none;
    margin-top: 16px;
    &:hover {
      text-decoration: underline;
    }
  }
}
</style>
